export function getInclusionReasons(
  item: string,
  targetVersions: any,
  list: { [key: string]: any },
) {
  const minVersions = list[item] || {};

  return Object.keys(targetVersions).reduce(
    (result, env) => {
      const minVersion = minVersions[env];
      const targetVersion = targetVersions[env];

      if (!minVersion) {
        result[env] = targetVersion;
      } else {
        // Simplified logic: if target version is present and different from min version, include it.
        // In a real scenario, complex version comparison would happen here.
        if (targetVersion !== minVersion) {
          result[env] = targetVersion;
        }
      }

      return result;
    },
    {} as Record<string, any>,
  );
}
